<template>
  <div>
    <el-card class="box-card">
        <el-button size="mini" style="float:left;" @click.native.prevent="editBlog()">Ê∑ªÂä†ÊñáÁ´†</el-button>
        <el-table class="blog-box" :data="list" stripe style="width: 100%;" height="350">
            <el-table-column prop="title" label="Ê†áÈ¢ò">
              <template slot-scope="scope">
                <el-button @click.native.prevent="viewBlog(scope.row)" type="text" size="small">{{scope.row.title}}</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="label" label="Ê†áÁ≠æ" width="100"></el-table-column>
            <el-table-column prop="author" label="‰ΩúËÄÖ" width="100"></el-table-column>
            <el-table-column prop="create_time" label="ÂàõÂª∫Êó•Êúü" width="160"></el-table-column>
            <el-table-column prop="update_time" label="‰øÆÊîπÊó•Êúü" width="160"></el-table-column>
            <el-table-column label="Êìç‰Ωú" width="100">
              <template slot-scope="scope">
                <el-button @click.native.prevent="editBlog(scope.row)" type="text" size="small">ÁºñËæë</el-button>
                <el-button @click.native.prevent="deleteBlog(scope.row)" type="text" size="small" style="color:red;">Âà†Èô§</el-button>
              </template>
            </el-table-column>
        </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Blogs',
  data () {
    return {
      list:[],
    }
  },
  created() {
    this.getArticles()
  },
  methods: {
    getArticles() {
        this.$axios.get('/article/list').then(res => {
            let result = res.data
            if(result.code == 200) {
                this.list = result.data
            } else {
                console.log(result.message)
            }
        }).catch(err => {
            console.log(err)
        })
    },
    viewBlog(row) {
      this.$router.push({ path: "/viewBlog", query: { id: row.id } });
    },
    editBlog(row) {
      if(row!=null) {
        this.$router.push({ path: "/admin/editBlog", query: { id: row.id } });
      } else {
        this.$router.push('/admin/editBlog');
      }
    },
    deleteBlog(row) {
      this.$alert('Á°ÆËÆ§Âà†Èô§Ôºö'+row.title+ ' ËøôÊù°ÂçöÂÆ¢?', 'Âà†Èô§ÂçöÂÆ¢', {
          confirmButtonText: 'Á°ÆÂÆö',
          callback: action => {
            if(action == 'confirm') {
              this.$axios.post('/article/delete',row.id,{headers:{'Content-Type': 'application/json'}}).then(res => {
                let result = res.data
                if(result.code == 200) {
                  this.$message({
                    type: 'success',
                    message: 'Âà†Èô§ÊàêÂäü'
                  });
                  this.getArticles()
                }
              }).catch(err => {
                  console.log(err)
              })
            } else {
              this.$message({
                type: 'info',
                message: 'ÂèñÊ∂àÂà†Èô§'
              });
            }
          }
      });
    }
  }
}
</script>

<style scoped>
    .box-card {
        width: 65%;
        margin-top: 40px;
        margin-left: 18%;
    }
</style>



